import React, { useMemo, forwardRef, useRef, useEffect, useState } from 'react';
import styled from 'styled-components'
import isObject from 'lodash/isObject'
import compact from 'lodash/compact';
import set from 'lodash/set';

import Box from '../components/Box'
import Flex from '../components/Flex'
import Text from '../components/Text';
import parseDate from 'utils/parseDate';
import PageTitle from './PageTitle';
import DramTimeline from './DramTimeline';

const StyledTable = styled.table`
th,
td {
  white-space: pre;
  padding: 0.5em;
  border: 1.5px solid white;
}
`

// const IndeterminateCheckbox = forwardRef(
//   ({ indeterminate, ...rest }, ref) => {
//     const defaultRef = useRef()
//     const resolvedRef = ref || defaultRef

//     useEffect(() => {
//       resolvedRef.current.indeterminate = indeterminate
//     }, [resolvedRef, indeterminate])

//     return (
//       <>
//         <input type="checkbox" ref={resolvedRef} {...rest} />
//       </>
//     )
//   }
// )

let group = []
const replaceSup = (t) => t.replace(/\[(\d+)\]/g, (...d) => `<sup>${d[1]}</sup>`)
const headerWidth = ['9em', '12.75em', '16.25em']
const noteBg = { mp: '#A4CCDB', cs: '#4D9185', es: '#DC888A', sample: '#2F6EBA' }
const bodybg = ['#B1B1B2', '#CACACA', '#E8E8E8']
const notefuc = (text) => (
  text[0] != '-' ? (
    <li><div dangerouslySetInnerHTML={{ __html: replaceSup(text) }} /></li>
  ) : <ol>{notefuc(text.substring(1, text.length))}</ol>
)

const DramDataTable = ({
  data,
  notes,
  bodyData,
  noteHeader,
  quater,
  year,
  type,
  headerRow,
  headerKeys,
  noteText,
  scale,
}) => {
  const [nmGroup, setNmGroup] = useState([]);
  const tableRef = useRef()
  const timelineData = useMemo(() => {
    const dateGetter = notes.map(key => headerKeys.find(d => d.Header === key))
    if (dateGetter.some(d => !d)) return []
    const timeline = data.map(d => dateGetter.map(({ columns }) => columns.map(({ accessor }, i) => {
      const dStr = accessor(d)
      return parseDate(dStr, i)
    })))
    return timeline
  }, [data, headerKeys, notes])
  const rowSpans = useMemo(() => bodyData?.map(d => d.slice(0, 1)[0]).reverse().reduce((res, h, i, { length }) => {
    if (h) {
      if (group.length) {
        set(res, [length - i - 1], group.length + 1)
        group = []
      } else {
        set(res, [length - i - 1], 1)
      }
    } else {
      group.push(i)
    }
    return res
  }, {}), [bodyData])
  useEffect(() => {
    if (tableRef.current) {
      const masterBox = tableRef.current.getBoundingClientRect()
      const cells = tableRef.current.querySelectorAll('.timeline-cell[data-nm')
      if (cells.length) {
        const cellGroup = []
        cells.forEach(cell => {
          const nm = cell.dataset.nm
          const boundBox = cell.getBoundingClientRect()
          const boxCenter = (boundBox.top + boundBox.height / 2 - masterBox.top) / scale
          let currentGroup = cellGroup[cellGroup.length - 1]
          if (nm !== currentGroup?.nm) {
            currentGroup = { nm, left: (boundBox.right - masterBox.left) / scale }
            cellGroup.push(currentGroup)
          }
          currentGroup.top = Math.min(currentGroup.top || boxCenter, boxCenter)
          currentGroup.bottom = Math.max(currentGroup.bottom || boxCenter, boxCenter)
        })
        setNmGroup(cellGroup)
      }
    }
  }, [bodyData])
  // console.log(equal)
  return (
    <Box position="relative" px="3.75em" width="1920px" height="1080px" ref={tableRef}>
      {nmGroup.length > 0 && (
        <Box.FullAbsolute>
          {nmGroup.map(({ nm, top, bottom, left }, i) => (
            <Flex
              key={nm + i}
              position="absolute"
              style={{ top, height: bottom - top, left }}
              border="1px solid"
              borderLeft="none"
              alignItems="center"
            >
              <Box
                style={{
                  writingMode: 'vertical-rl',
                  textRrientation: 'mixed',
                }}
                transform="translateX(120%)"
              >
                {nm}
              </Box>
            </Flex>
          ))}
        </Box.FullAbsolute>
      )}
      <PageTitle>{data[0][0]}</PageTitle>
      <Flex pt="8em">
        <StyledTable width="100%" style={{ position: "relative" }}>
          <thead style={{ fontSize: '1.25rem' }}>
            <tr>
              {headerRow.map(({ Header, columns }, i) => (
                <Box as="th" width={headerWidth[i]} color="white" bg={i < 3 ? '#234B69' : '#C63B38'} rowSpan={!columns && 2} colSpan={columns?.length || 1} key={i}>{Header}</Box>
              ))}
            </tr>
            <tr>
              {headerRow.map(({ columns }) => columns?.map(({ Header }, i) => (
                <Box as="th" bg="#CACACA" width="4em" key={i}>{Header}</Box>
              )))}
            </tr>
          </thead>
          {/* Apply the table body props */}
          <tbody style={{ fontSize: '1.125rem' }}>
            {bodyData.map((body, i) => (
              <Box as="tr" key={i}>
                {body.map((d, k) => d && (isObject(d) ? (
                  <Box
                    as="td"
                    colSpan={d.col}
                    style={{ background: 'linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3))' }}
                    key={k}
                    position="relative"
                    overflow="visible"
                    className="timeline-cell"
                    data-nm={d.nm || undefined}
                  >
                    <DramTimeline
                      data={timelineData[i]}
                      startYear={year}
                      startQuarter={type === 3 ? quater : 1}
                      yearSpan={type}
                      colors={notes.map(n => noteBg[n.toLocaleLowerCase()])}
                    />
                    <Box position="relative" pl="8em">
                      <span
                        dangerouslySetInnerHTML={{__html: replaceSup(d.text)}}
                      />
                      {d.nrnd && <Text.Inline fontWeight="bold" color="#234b69" pl="1em">*NRND</Text.Inline>}
                    </Box>
                  </Box>
                ) : (
                  <Box
                    as="td"
                    fontWeight={!k && 'bold'}
                    bg={bodybg[k]}
                    // color={d === 'NOTE' ? 'white' : 'black'}
                    textAlign={k === 2 ? 'left' : 'center'}
                    rowSpan={!k && rowSpans[i]}
                    // colSpan={d ==='NOTE' && compact(noteHeader).length}
                    key={k}
                  >
                    <Box
                      px="1em"
                      dangerouslySetInnerHTML={{ __html: replaceSup(d) }}
                    />
                  </Box>
                )))}
              </Box>
            ))}
          </tbody>
        </StyledTable>
        <Flex flexDirection="column" ml="4.25em" width="19.375em">
          <Box border="1.5px solid white" bg="white" p="0.5rem">　</Box>
          <Flex mb="1em">
            {notes.map((d, i) => (
              <Box fontSize="1.25rem" width="100%" fontWeight="bold" textAlign="center" bg={noteBg[d.toLocaleLowerCase()]} color="white" p="0.5rem" key={i}>{d}</Box>
            ))}
          </Flex>
          <Box bg="#9FA0A0" fontSize="1.125rem" textAlign="center" color="white" p="0.5em" fontWeight="bold">NOTE</Box>
          <Box flex={1} p="0.5rem" style={{ background: 'linear-gradient(0deg, rgba(227,227,227,0.85), rgba(238,238,238,0.3))' }}>
            {noteText[0] && (
              <Box as="ul" ml="-1.5rem">
                {noteText.map(notefuc)}
              </Box>
            )}
          </Box>
        </Flex>
      </Flex>
    </Box>
  )
};

export default DramDataTable;
