import React, { useState, useMemo } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import { getMonth, getQuarter, getYear } from 'date-fns';
import { floor, isNumber, range, set, size } from 'lodash'
import { Link as GatsbyLink } from 'gatsby'

import Container from '../components/Container'
import Box from '../components/Box'
import Flex from '../components/Flex'
import Link from '../components/Link'
import Button from '../components/Button'
import Input from '../components/Input'
import Pagination from '../components/Pagination'
import { responsive } from '../components/ThemeProvider/theme';

import DramDataTable from './DramDataTable'
import PortfolioPage from './PortfolioPage'
import SheetLoader from './SheetLoader'
import PageControl from './PageControl'
import Downloader from './Downloader'

import CompareSummary from './CompareSummary';
import useTableHeader from 'hooks/useTableHeader';
import PageSimulator from './PageSimulator';

// const colorKeys = [{}, {}]
// const getColor = (name, i = 0) => {
//   const keys = colorKeys[i]
//   if (typeof keys[name] !== 'number') {
//     keys[name] = (max(Object.values(keys)) || 0) + 1
//   }
//   return keys[name]
// }

// const switchHeader = (d) => d.map((d, i) => ({

// }))


const SheetData = ({ selectedSheet, data: rawData, compareSheetData, name }) => {
const types = useMemo(() => [
  { title: '3年', value: 3 },
  { title: '7年', value: 7 },
].concat(name === 'flash' ? [
  { title: '總表', value: 'portfolio' },
  { title: '大總表', value: 'compact' },
] : [
  { title: '3年總表', value: 'portfolio' },
  { title: '7年總表', value: 'portfolio-7' },
]), [name])

  const notes = useMemo(() => name === 'flash' ? ['Sample', 'MP'] : ['ES', 'CS', 'MP'], [name])
  const [type, setType] = useState(3)
  const [downloading, setDownloading] = useState()
  const [startYear, setStartYear] = useState(new Date().getFullYear())
  // const [selectedRow, setSelectedRow] = useState({})
  const [page, setPage] = useState(0)
  const year = getYear(new Date())
  const quater = getQuarter(new Date())
  const data = useMemo(() => {
    if (!rawData) return null
    if (!rawData[0][0]) return rawData.slice(1)
    return rawData
  }, [rawData])
  const half = getMonth(new Date()) > 6 ? 2 : 1
  const roadmapheaderRow = useMemo(() => data && isNumber(type) && (type < 7 ?
    [
      data[0][data[0].findIndex(d => d === 'Density')],
      data[0][data[0].findIndex(d => d === 'Product')],
      'Description',
    ...range(type * 4).map((n) => {
      if (!n) {
        return year
      }
      // console.log(Boolean((quater + n) % 4))
      if ((quater + n) % 4 === 1) {
        return year + floor((quater + n) / 4)
      } else {
        return ''
      }
    })] : [
      data[0][data[0].findIndex(d => d === 'Density')],
      data[0][data[0].findIndex(d => d === 'Product')],
      'Description',
      ...range(type * 2).map((n) => {
        if (!n) {
          return year
        }
        // console.log(Boolean((quater + n) % 4))
        if ((half + n) % 2 === 1) {
          return year + floor((half + n) / 2)
        } else {
          return ''
        }
      })
    ])
    , [data, type, year, quater, half])
  const doubleHeader = useMemo(() => data && data[0].some(h => !h), [data])
  const headerKeys = useTableHeader(data)
  const headerRow = useMemo(() => {
    if (!data || typeof type !== 'number') return []
    if (doubleHeader) {
      let groupMemo = []
      // 空白代表要group ['a', 'b', '', 'c']
      // b後面的空白要跟b group一起，所以先反轉陣列，從後面檢查回來，只要是空白，就等到下一個值出現然後把他們group起來
      return roadmapheaderRow.slice(0).reverse().reduce((all, h, i) => {
        // 檢查有沒有值
        if (h) {
          // 如果年份只有一季，但還是要有第二列
          if (isNumber(h) && !groupMemo.length) {
            all.push({
              Header: h,
              columns: [{
                Header: type < 7 ? (i ? 'Q4' : 'Q1') : (i ? 'H2' : 'H1'),
                id: `${h}-${1}`,
              }],
            })
            return all
          }
          // 檢查有沒有之前先標記要變成group的
          if (groupMemo.length) {
            // if true 把現在這一格也加入group
            groupMemo.push(i)
            all.push({
              Header: h,
              columns: groupMemo.slice().reverse().map((j, i, { length }) => ({
                Header: type < 7 ? `Q${(4 - length) + i + 1}` : `H${i + 1}`,
                // accessor: d => d[colLength - j - 1],
                id: `${h}-${i + 1}`,
              })),
            })
            // 清空group的標記
            groupMemo = []
          } else {
            // if false 就是一般的header
            all.push({ Header: h, id: i })
          }
        } else {
          // 沒有值，之後要跟別人group，先暫存起來
          groupMemo.push(i)
        }
        return all
      }, [])
        .reverse() // 最後記得再反轉回來
    }
  }, [data, doubleHeader, type])
  const tableData = useMemo(() => data ? data.slice(doubleHeader ? 2 : 1) : [], [data, doubleHeader])
  const pages = useMemo(() => {
    return tableData.reduce((all, row) => {
      let i = all.length - 1
      if (row[0]) {
        i = all.push([]) - 1
      }
      all[i]?.push(row)
      return all
    }, [])
  }, [tableData])
  const noteText = useMemo(() => {
    if (!data) return []
    const noteIndex = data[0].findIndex(d => d === 'Note')
    if(noteIndex < 0) return []
    // console.log(pages && pages?.[page][0][noteIndex])
    return pages && pages?.[page][0][noteIndex].split('\n')
  }, [data, pages, page])
  const bodyData = useMemo(() => data && pages[page]?.reduce((res, d, k) => {
    res.push([
      d[data[0].findIndex(d => d === 'Density')],
      d[data[0].findIndex(d => d === 'Product')],
      (name !== 'flash' ? `${d[data[0].findIndex(d => d === 'I/O Width')] + ' ' + d[data[0].findIndex(d => d === 'Voltage')] + `\n` + d[data[0].findIndex(d => d === 'Speed')]}` : (
        [data[0].findIndex(d => d === 'Voltage'), data[0].findIndex(d => d === 'Description')].map(i => d[i]).join(',\n')
      )),
      {
        text: name !== 'flash' && [
          data[0].findIndex(d => d === 'Technology Node (nm)'),
          data[0].findIndex(d => d === 'Components'),
          data[0].findIndex(d => d === 'Package')
        ].map(i => d[i]).join(', '),
        col: type > 3 ? 14 : 12,
        nrnd: d[data[0].findIndex(d => d === 'NRND')] === 'TRUE',
        nm: name === 'flash' && d[data[0].findIndex(d => /Technology/i.test(d))],
      },
    ])
    return res
  }, []), [pages, page, type, data])
  // const noteHeader = useMemo(() => data && pages[page]?.reduce((res, d) => {
  //   d.slice(data?.[0].findIndex(header => header == 'ES'), (data?.[0].findIndex(header => header == 'MP') + 2)).forEach((content, i) => {
  //     if(content && !res.includes(notes[floor(i/2)])) {
  //       set(res, [floor(i/2)], notes[floor(i/2)])
  //     }
  //   })
  //   return res
  // }, []) ,[data, pages, page])
  const compareData = useMemo(() => (compareSheetData) ? compareSheetData.slice(doubleHeader ? 2 : 1) : [], [compareSheetData, doubleHeader])
  const [compareMark, comparePages] = useMemo(() => {
    if (!size(compareData)) return [null, null]
    let page = 0
    return compareData.reduce(([all, pages], row) => {
      if (row[0]) page += 1
      all[row[1]] = row
      pages[row[1]] = page
      return [all, pages]
    }, [{}, {}])
  }, [compareData])
  // console.log(noteText)
  return (
    <Tabs style={{ marginTop: '2em' }}>
      <TabList>
        <Tab>簡報產生器</Tab>
        <Tab disabled={!compareData.length}>更新對照表</Tab>
      </TabList>
      <TabPanel>
        <Flex my="1em" alignItems="center">
          {/* <Text mr="0.5em">起始年分</Text>
            <Box width="12em">
              <input
                style={{ lineHeight: 2 }}
                type="number"
                value={startYear}
                onChange={e => setStartYear(e.target.value)}
              />
            </Box> */}
          {/* <Input onChange={(e) => setIndex(e.target.value)} />
          <Box>{arrayfunc(+index)}</Box> */}
          {selectedSheet && types.map((t, i) => (
            <Button.Outline
              active={type === t.value}
              mx="0.25em"
              onClick={() => setType(t.value)}
              key={i}
            >{t.title}</Button.Outline>
          ))}
        </Flex>
        {typeof type === 'number' && pages && pages[page] && (
          <>
            <Pagination
              pageCount={pages.length}
              pageRangeDisplayed={5}
              marginPagesDisplayed={2}
              onPageChange={({ selected }) => setPage(selected)}
            />
            <PageSimulator name={`${name}-${type}y-${page + 1}`}>
              <DramDataTable
                headerKeys={headerKeys}
                data={pages[page]}
                headerRow={headerRow}
                doubleHeader={doubleHeader}
                bodyData={bodyData}
                quater={quater}
                year={year}
                type={type}
                half={half}
                noteText={noteText}
                // noteHeader={noteHeader}
                notes={notes}
              />
            </PageSimulator>
            {/* <PageControl
              key={selectedSheet}
              allData={tableData}
              headerRow={headerRow}
              page={page}
              data={pages[page]}
              compareMark={compareMark}
              sheetName={selectedSheet}
              startYear={year}
            /> */}
            {/* <Button onClick={() => setDownloading(true)} disabled={downloading}>
                批次下載{downloading ? '中...' : ` (${pages.length}頁)` }
              </Button>
              {downloading && (
                <Downloader
                  allData={tableData}
                  headerRow={headerRow.slice(0, headerRow.length - 2)}
                  pages={pages}
                  compareMark={compareMark}
                  sheetName={selectedSheet}
                  startYear={startYear}
                  onDownloaded={() => setDownloading(false)}
                />
              )} */}
          </>
        )}
        {/^portfolio/.test(type) && data && (
          <PortfolioPage
            data={data}
            name={name}
            headerKeys={headerKeys}
            filterKey={`Portfolio.${name === 'flash' ? 'Sub' : (type === 'portfolio-7' ? '7yrs' : '3yrs')}`}
          />
        )}
        {type === 'compact' && data && (
          <PortfolioPage data={data} name={name} headerKeys={headerKeys} filterKey="Portfolio.All" variant="compact" />
        )}
      </TabPanel>
      {/* <TabPanel>
        <CompareSummary
          data={pages}
          headerRow={headerRow}
          compareMark={compareMark}
          sheetName={selectedSheet}
          comparePages={comparePages}
        />
      </TabPanel> */}
    </Tabs>
  )
}

const DramPage = ({ name = 'dram' }) => {
  return (
    <Container>
      <Link as={GatsbyLink} to="/">
        {`< Home`}
      </Link>
      <SheetLoader name={name}>
        <SheetData />
      </SheetLoader>
    </Container>
  )
}

export default DramPage
